const arr = [1, 2, 3];
arr.length = 0; //     - GC  .
const user = { name: "Alex", age: 30};
delete user.age; // \`age\`  - GC    .

//  5   
function* generateData() {
    const data = "big data";
    yield data;
}
const gen = generateData();
gen.next(); //  
gen.return(); //    - \`data\`   .
