import { useSelector } from 'react-redux';

function Cart(){
    const cartItems = useSelector(state => state.cart);

    return (
        <ul>
            {cartItems.map(item => (
                <li key={item.id}>{item.name}</li>
            ))}
        </ul>
    );
}
