export const Task_4 = {
  title: "Task 4",
  id: 1003,
  children: [
    {
      title: "Polish alphabet",
      type: "text",
      description: `
            There are 32 letters in the Polish alphabet: 9 vowels and 23 consonants.
            Your task is to change the letters with diacritics:
             -> a,
             -> c,
             -> e,
             -> l,
             -> n,
             -> o,
             -> s,
             -> z,
             -> z
            and print out the string without the use of the Polish letters.
            For example:
            "Jdrzej Bdziski"  -->  "Jedrzej Bladzinski"
            `,
      code: `
function correctPolishLetters (string) {
const ArrayDiacriticsLetters = [
{ key: '', value: 'a' },
{ key: '', value: 'c' },
{ key: '', value: 'e' },
{ key: '', value: 'l' },
{ key: '', value: 'n' },
{ key: '', value: 'o' },
{ key: '', value: 's' },
{ key: '', value: 'z' },
{ key: '', value: 'z' }
]

let spreadString = [...string]
let result = []
for(let i = 0; i < string.length; i++){
for(let j = 0; j < ArrayDiacriticsLetters.length; j++){
if(spreadString[i] === ArrayDiacriticsLetters[j].key){
  result.push(ArrayDiacriticsLetters[j].value)
}
}
if(!(result[i]) === true){
result.push(spreadString[i])
} 
}

return result.join('')
}

// or

function correctPolishLetters (str) {
return str
.replace(//g, 'a')
.replace(//g, 'c')
.replace(//g, 'e')
.replace(//g, 'l')
.replace(//g, 'n')
.replace(//g, 'o')
.replace(//g, 's')
.replace(//g, 'z')
.replace(//g, 'z')
}

// or

describe("Tests", () => {
it("test", () => {
assert.strictEqual(correctPolishLetters("Jdrzej Bdziski"), "Jedrzej Bladzinski");
assert.strictEqual(correctPolishLetters("Lech Wasa"), "Lech Walesa");
assert.strictEqual(correctPolishLetters("Maria Skodowska-Curie"), "Maria Sklodowska-Curie");
});
});

// or

const correctPolishLetters = str => {
const diacritics = {'':'a','':'c','':'e','':'l','':'n','':'o','':'s','':'z','':'z'};

return str.replace(/[]/g, match => diacritics[match])
}
            `,
    },
  ],
};
