      `,
      code: `
function Greeting(props) {
  return <p>Hello, {props.name}!</p>;
}

Greeting.defaultProps = {
  name: 'Guest'
};

// 
function App() {
  return (
    <>
      <Greeting name="Alice" /> <!-- : Hello, Alice! -->
      <Greeting /> <!-- : Hello, Guest! -->
    </>
  );
}
