      `,
      code: `
import PropTypes from 'prop-types';

function Greeting({ name, age }) {
  return (
    <div>
      <p>Name: {name}</p>
      <p>Age: {age}</p>
    </div>
  );
}

Greeting.propTypes = {
  name: PropTypes.string.isRequired, //  
  age: PropTypes.number               //  ()
};

// 
function App() {
  return <Greeting name="Alice" age={30} />;
}
