      title: "  do...while + swapped",
      type: "text",
      description: `
            <p>  do...while:   ,        .</p>
            <p>   (  ).</p>
            <p>        (swapped  false),   ,    .</p>
            <p>  (n^2)</p>
          `,
      code: `
function bubbleSort(arr) {
  const n = arr.length;
  let swapped;
  do {
    swapped = false;
    for (let i = 0; i < n - 1; i++) {
      if (arr[i] > arr[i + 1]) {
        [arr[i], arr[i + 1]] = [arr[i + 1], arr[i]];
        swapped = true;
      }
    }
  } while (swapped);
  return arr;
}
          `,
